@import "@/styles/main.scss";


















































































































































































































































.contact-modal{
    position: fixed;
    display: none;
    right: 5%;
    bottom: 75px;
    width: 350px;
    min-height: 400px;
    z-index: 1000000;
    padding: 30px;
    border-radius: 4px;
    background-color: #ffffff;
    box-shadow: hsla(0, 0%, 0%, 0.12) 0px 0px 20px 0px;

    form{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
        align-items: flex-start;
        height: 340px;
        padding-bottom: 15px;
        z-index: 1000000;

        .form-error-txt{
            font-size: 0.8rem;
            font-size: 400;
            color: #d9534f;
            // margin-top: 5px;
            text-align: left;
        }


        input{
            width: 100%;
            margin-bottom: 4px;
            -webkit-appearance: none;
            border: 1px solid hsla(0, 0%, 65%, 0.4);
            border-radius: 5px;
            background-color: transparent;
            padding: 12px;
            font-size: 14px;
            animation-duration: 0.3s;

            &:focus{
                outline: none;
                border-bottom: 2px solid #225E9B;
                animation-name: pulse;
                animation-duration: 0.3s;

                &::placeholder{
                    color: hsla(0, 0%, 65%, 0.4) !important;
                    -webkit-transform: translateX(10px);
                    -moz-transform: translateX(10px);
                    transform: translateX(10px);
                  }
            }

            &::-webkit-input-placeholder{
                transition: all 0.3s ease;
                color: hsla(0, 0%, 65%, 0.4);
                font-weight: 200 ;
                font-size: 0.9rem;
            }

            &::-ms-input-placeholder{
                transition: all 0.3s ease;
                color: hsla(0, 0%, 65%, 0.4) !important;
                font-weight: 200 !important;
                font-size: 0.9rem;
            }
        }

        textarea{
            width: 100%;
            -webkit-appearance: none;
            border: 1px solid hsla(0, 0%, 65%, 0.4);
            border-radius: 5px;
            margin-bottom: 4px;
            resize: none;
            background-color: transparent;
            padding: 12px;
            font-size: 14px;
            animation-duration: 0.3s;


            &:focus{
                outline: none;
                border-bottom: 2px solid #225E9B;
                animation-name: pulse;
                animation-duration: 0.3s;
                &::placeholder{
                    color: hsla(0, 0%, 65%, 0.4) !important;
                    -webkit-transform: translateX(10px);
                    -moz-transform: translateX(10px);
                    transform: translateX(10px);
                  }
            }

            &::-webkit-input-placeholder{
                transition: all 0.3s ease;
                color: hsla(0, 0%, 65%, 0.4) ;
                font-weight: 200 ;
                font-size: 0.9rem;
            }

            &::-ms-input-placeholder{
                transition: all 0.3s ease;
                color: hsla(0, 0%, 65%, 0.4) !important;
                font-weight: 200 !important;
                font-size: 0.9rem;
            }
        }

        .button-holder{
            width: 100%;
            margin-top: 15px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .cancel-button{
                border: none;
                border: 2px solid transparent;
                background:
                linear-gradient(to right, #ffffff, #ffffff),
                linear-gradient(to right, #F07A6A , #E8496D);
                background-clip: padding-box, border-box;
                background-origin: padding-box, border-box;
                width: 131px;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 8px 15px;
                border-radius: 5px;
                color: #F07A6A;
                font-size: 1rem;
                transition: all 0.3s ease-in-out;
                font-weight: 600;

                &:hover{
                    box-shadow: 0 10px 10px 0 rgba($color: #F07A6A, $alpha: .14), 0 3px 3px 0 rgba($color: #F07A6A, $alpha: .06);

                }

            }

            .send-button{
                width: 131px;
                background: linear-gradient(to right, #2B92E1 , #68E3F1);
                letter-spacing: 0.1rem;
                border: none;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px 15px;
                border-radius: 5px;
                color: #ffffff;
                font-size: 1rem;
                font-weight: 600;
                transition: all 0.3s ease-in-out;

                p{
                    display:flex;
                    justify-content:center;
                    align-items:center;
                    letter-spacing: 0.02rem;
                    width:100%;
                    margin:0;
                    padding:0
                }

                &:hover{
                    box-shadow: 0 10px 10px 0 rgba($color: #2B92E1, $alpha: .14), 0 3px 3px 0 rgba($color: #2B92E1, $alpha: .06);
                }
            }
        }
    }
}

.loading{
    color: #ffffff;
    font-size: 1.4rem;
    animation:
        loading-rotate 1s infinite both;

        @keyframes loading-rotate {
            0% {
                transform: rotateZ(0deg);
            }
            100% {
                transform: rotateZ(360deg);
            }
        }
}

@media (max-width: 450px){
    .contact-modal{
        right: 2% !important;
    }
}

@media (max-width: 380px){
    .contact-modal{
        right: 2% !important;
        width: 300px;
        // height: 320px;

    }
}
